// module
import IsMobile from "../utils/isMobile";

// options
const speed = 0.9;
let totalDistance = 0;

// set marquee
function setMarquee() {
    if (IsMobile()) return;
    const parentElem = document.querySelector(".marquee");
    const childElem = parentElem.children[0];
    const copyText = childElem.innerText;
    childElem.append(copyText);

    moveMarquee();
}

// move marquee
function moveMarquee() {
    const target = document.querySelector(".marquee_text");
    target.style.marginTop = `${totalDistance}px`;

    if (totalDistance > target.clientWidth / 2) totalDistance = 0;
    totalDistance += speed;

    requestAnimationFrame(moveMarquee);
}

export default setMarquee;
